// Link <button>

[type="submit"] {
  &.vl-link {
    &.vl-link--icon {
      padding-left: 1.8rem;
    }
  }
}

.vl-link {
  @include reset-button;
  @include vl-u-link-underline;
  cursor: pointer;
  font-weight: inherit;
  text-align: left;
  font-family: inherit;
  font-size: inherit;

  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
  }

  &--bold {
    font-weight: 500;
    text-decoration: none;

    &:hover,
    &:focus,
    &:active {
      text-decoration: underline;
    }
  }

  &--small {
    font-size: 1.6rem;
  }

  &--large {
    font-size: 2rem;
  }

  // Block modifier
  &--block {
    display: block;
  }
}

.vl-link__icon {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  overflow: hidden;
  text-decoration: none;
  color: inherit;
  flex: 0 0 auto;
  margin-top: -0.1em;

  &::before,
  &::after {
    display: inline-block;
  }

  &--before {
    padding-right: 0.4rem;
  }

  &--after {
    padding-left: 0.4rem;
  }

  &--light {
    color: $vl-placeholder-color;
  }

  &--borderless {
    overflow: visible;

    &::after {
      position: absolute;
      display: block;
      height: .1rem;
      left: 0;
      width: 100%;
      bottom: 0;
      margin-bottom: -.3rem;
      background-color: $vl-page-bg;
      content: "";
    }
  }

  &.vl-vi-external {
    font-size: 1em;
  }
}
