// ==========================================================================
// Mapped colors
// Color variables based on their function or role, to see the color values
// see _colorsRaw.scss
// ==========================================================================
$vl-page-bg: $vl-white !default;

// ================================
// css variable colors
// ================================
:root {
  --vl-theme-primary-color: #{$vl-yellow};
  --vl-theme-primary-color-60: #{mix($vl-yellow, $vl-white, 60%)};
  --vl-theme-primary-color-70: #{mix($vl-yellow, $vl-white, 70%)};
  --vl-theme-primary-color-rgba-30: #{rgba($vl-yellow, .3)};

  --vl-theme-fg-color: #{$vl-mine-shaft};
  --vl-theme-fg-color-60: #{mix($vl-mine-shaft, $vl-white, 60%)};
  --vl-theme-fg-color-70: #{mix($vl-mine-shaft, $vl-white, 70%)};
}

// ================================
// Text colors
// ================================
$vl-text-color: $vl-mine-shaft !default;
$vl-alt-text-color: lighten($vl-mine-shaft, 10%) !default;
$vl-sub-text-color: $vl-gray !default;
$vl-placeholder-color: $vl-silver-chalice !default;
$vl-icon-color: $vl-silver-chalice !default;
$vl-annotation-color: $vl-bluey-gray !default;

// ================================
// Default widely used colors
// ================================
$vl-action-color: $vl-science-blue !default;
$vl-action-color-10: mix($vl-action-color, $vl-page-bg , 10%) !default;
$vl-action-color-65: mix($vl-action-color, $vl-page-bg , 65%) !default;
$vl-action-fg-color: mix($vl-action-color, $vl-white, 10%) !default;
$vl-action-hover-color: $vl-curious-blue !default;
$vl-action-active-color: $vl-endeavour !default;
$vl-action-active-border-color: $vl-regal-blue !default;
$vl-action-text-color: $vl-action-color !default;
$vl-action-text-hover-color: $vl-action-hover-color !default;
$vl-action-text-active-color: $vl-action-active-color !default;
$vl-action-disabled-text-color: $vl-bluey-gray !default;
$vl-action-secondary-color: $vl-dark-sky-blue !default;
$vl-action-tertiary-color: $vl-loblolly !default;
$vl-action-tertiary-hover-color: darken($vl-loblolly, 20%) !default;
$vl-action-tertiary-hover-border-color: $vl-dark-sky-blue !default;
$vl-accent-color: var(--vl-theme-primary-color) !default;
$vl-accent-color-60: var(--vl-theme-primary-color-60) !default;
$vl-accent-color-70: var(--vl-theme-primary-color-70) !default;
$vl-accent-color-rgba-30: var(--vl-theme-primary-color-rgba-30) !default;
$vl-accent-fg-color: $vl-page-bg !default;
$vl-accent-alt-color: darken($vl-yellow, 5%) !default;
$vl-border-color: $vl-ghost !default;
$vl-border-alt-color: $vl-link-water !default;
$vl-outline-color: $vl-amber !default;

// ================================
// Backgrounds
// ================================
$vl-alt-bg: $vl-white-lilac !default;
$vl-alt-hover-bg: $vl-athens-gray-light !default;
$vl-overlay-bg: rgba($vl-page-bg, 0.8) !default;
$vl-alt-bg-dark: $vl-bluey-gray !default;

// ================================
// States
// ================================
$vl-error-color: $vl-punch !default;
$vl-error-fg-color: mix($vl-error-color, $vl-white, 10%) !default;
$vl-error-border-color: mix($vl-error-color, $vl-white, 40%) !default;
$vl-error-hover-color: $vl-tall-poppy !default;
$vl-error-secondary-color: $vl-faded-red !default;
$vl-error-secondary-fg-color: mix($vl-faded-red, $vl-white, 10%) !default;
$vl-error-text-color: $vl-error-color !default;

$vl-warning-color: $vl-amber !default;
$vl-warning-text-color: $vl-amber !default;
$vl-warning-fg-color: mix($vl-warning-color, $vl-white, 10%) !default;
$vl-warning-border-color: mix($vl-warning-color, $vl-white, 40%) !default;
$vl-warning-hover-color: darken($vl-warning-color, 5%) !default;
$vl-warning-secondary-color: $vl-off-yellow !default;

$vl-success-color: $vl-chateau-green !default;
$vl-success-hover-color: $vl-success-color !default;
$vl-success-fg-color: mix($vl-success-color, $vl-white, 10%) !default;
$vl-success-border-color: mix($vl-success-color, $vl-white, 40%) !default;
$vl-success-secondary-color: $vl-rum-swizzle !default;
$vl-success-text-color: $vl-success-color !default;

$vl-info-color: $vl-athens-gray !default;
$vl-info-hover-color: $vl-info-color !default;
$vl-info-fg-color: $vl-athens-gray !default;
$vl-info-secondary-color: $vl-rum-swizzle !default;
$vl-info-text-color: $vl-info-color !default;
$vl-info-border-color: $vl-ghost !default;

$vl-bordered-content-border-color: $vl-heather !default;
$vl-subtext-color: $vl-alt-text-color !default;
$vl-marker-bg-color: $vl-yellow !default;
$vl-marker-fg-color: $vl-text-color !default;
$vl-progress-color: $vl-pink !default;
$vl-progress-bg-color: $vl-geyser !default;
$vl-progress-bg-alt-color: $vl-ghost !default;

// ================================
// Disabled
// ================================
$vl-disabled-color: $vl-bluey-gray !default;
$vl-disabled-color-dark: $vl-heather !default;
$vl-disabled-bg-color: #cbd2d9 !default;
$vl-disabled-text-color: var(--vl-theme-fg-color-70) !default;

// ================================
// Regions
// ================================
$vl-alt-region-bg: $vl-alt-bg !default;

// ================================
// Forms
// ================================
$vl-input-border-color: $vl-bluey-gray !default;
$vl-input-border-color-alt: $vl-mischka !default;
$vl-input-hover-border-color: rgba($vl-science-blue, .65) !default;
$vl-input-hover-background-color: rgba($vl-perano, .3) !default;
$vl-input-disabled-background-color: $vl-porcelain !default;
$vl-input-disabled-border-color: $vl-heather !default;
$vl-input-disabled-text-color: $vl-bluey-gray !default;
$vl-input-checked-background-color: $vl-yellow !default;
$vl-input-checked-border-color: $vl-black !default;
$vl-input-unchecked-background-color: $vl-white !default;
$vl-input-unchecked-disabled-background-color: $vl-porcelain !default;
$vl-input-unchecked-border-color: $vl-heather !default;
$vl-label-color-light: $vl-bluey-gray !default;
