$vl-alert-padding: 2rem;
$vl-alert-padding-small: 1.5rem;
$vl-alert-padding-large: 5rem 5.5rem;

.vl-alert {
  display: flex;
  position: relative;
  background-color: $vl-info-fg-color;
  border: 0.1rem solid $vl-info-border-color;
  padding: 2.5rem 3rem;

  @include respond-to(small) {
    padding: $vl-alert-padding-small;
  }

  &__close {
    display: flex;
    position: absolute;
    border: 0;
    cursor: pointer;
    top: 0.9rem;
    right: 0.9rem;
    padding: 0.5rem;
    font-size: 1.6rem;
    overflow: hidden;

    &:hover,
    &:focus {
      color: $vl-action-hover-color;
    }

    > .vl-vi {
      display: inline-flex;
    }
  }

  &__content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    flex-basis: 100%;
  }

  &__icon {
    flex: 0 0 auto;
    margin-right: $vl-alert-padding;
    font-size: 2.4rem;
    line-break: 1.1;
    line-height: 1;

    @include respond-to(small) {
      margin-right: $vl-alert-padding-small;
      font-size: 1.6rem;
    }
  }

  &__title {
    @include title(6);
  }

  &__message {
    margin-top: 1rem;
  }

  &__actions {
    margin-top: 1.5rem;
  }

  &--cta {
    &::before {
      content: "";
      position: absolute;
      top: -0.1rem;
      left: -0.1rem;
      width: calc(100% + 0.2rem);
      height: 0.3rem;
      background: $vl-action-color;
    }
  }

  &--error {
    background-color: $vl-error-secondary-fg-color;
    background-color: $vl-error-fg-color;
    border-color: $vl-error-border-color;

    .vl-alert__icon {
      color: $vl-error-color;
    }
  }

  &--warning {
    background-color: $vl-warning-fg-color;
    border-color: $vl-warning-border-color;

    .vl-alert__icon {
      color: $vl-warning-color;
    }
  }

  &--success {
    background-color: $vl-success-fg-color;
    border-color: $vl-success-border-color;

    .vl-alert__icon {
      color: $vl-success-color;
    }
  }

  &--naked {
    background: transparent;
    padding: 0;
    border: 0;

    .vl-alert__content {
      display: block;
    }

    .vl-alert__title {
      > * {
        display: inline;
      }
    }

    p {
      display: inline;
    }
  }

  &--small {
    font-size: 1.4rem;
    line-height: 1.2;
    padding: 1.5rem;

    .vl-alert__icon {
      width: 2rem;
      height: 2rem;
      margin-right: 1.5rem;
    }

    .vl-alert__actions {
      margin-top: 1rem;
    }
  }

  // Large
  &--large {
    padding: $vl-alert-padding-large;

    .vl-alert__title {
      font-size: 2.2rem;
    }
  }
}

.vl-alert__image {
  width: 8rem;
  height: auto;
  margin-right: 4rem;
}
