// sass-lint:disable force-pseudo-nesting no-important no-duplicate-properties no-vendor-prefixes no-color-literals no-color-keywords
$vl-region-overlap-black-transparent: transparent;
$vl-region-overlap-bg: rgba($vl-white-lilac, 1);

.vl-region {
  margin: 0 auto;
  padding: $vl-spacing-medium 0 $vl-spacing-large;

  @include respond-to(small) {
    padding: $vl-spacing-medium 0;
  }

  &.vl-region--no-space {
    padding: 0;
  }

  &.vl-region--no-space-bottom {
    padding-bottom: 0;
  }

  &.vl-region--no-space-top {
    padding-top: 0;
  }
}

.vl-region:not(.vl-region--alt) + .vl-region:not(.vl-region--alt) {
  padding-top: 0;
}

// color variants
.vl-region--alt {
  background-color: $vl-alt-region-bg;
}

.vl-region--overlap {
  background: linear-gradient(
    to bottom,
    $vl-region-overlap-black-transparent calc(50% - 30px),
    $vl-region-overlap-bg calc(50% - 30px),
    $vl-region-overlap-bg 100%,
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#000000',GradientType=0 );

  .vl-layout {
    border: 1px $vl-border-color solid;
    padding-top: 50px;
    padding-bottom: 50px;
    background: $vl-white;

    @media only screen and (max-width: $vl-page-max-width-wide + 15px) {
      margin: 15px;
    }

    @include respond-to(medium) {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }
}

.vl-region--overlap + .vl-region--alt {
  padding-top: 0 !important;
}

// two consequent different regions
.vl-region:not(.vl-region--alt) + .vl-region--alt,
.vl-region--alt + .vl-region:not(.vl-region--alt) {
  padding-top: $vl-spacing-large;

  @include respond-to(small) {
    padding-top: $vl-spacing-medium;
  }
}

.vl-region:first-child {
  padding-top: $vl-spacing-large;

  @include respond-to(small) {
    padding-top: $vl-spacing-normal;
  }
}

.vl-region--small {
  padding: $vl-spacing-small 0;

  @include respond-to(small) {
    padding: $vl-spacing-normal 0;
  }
}

.vl-region--medium {
  padding: $vl-spacing-medium 0;

  @include respond-to(small) {
    padding: $vl-spacing-normal 0;
  }
}

.vl-region--bordered + .vl-region--bordered {
  border-top: 1px solid $vl-alt-region-bg;

  &.vl-region--alt {
    border-top-color: $vl-white;
  }
}
