$vl-property-data-width-column: 75%;
$vl-property-label-width-column: 100% - $vl-property-data-width-column;

// Properties
.vl-properties {
  // Properties title
  &__title {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;

    @include respond-to(small) {
      font-size: $vl-base-font-size;
    }
  }

  // Properties list
  &__list {
    display: flex;
    flex-wrap: wrap;
  }

  // Properties label / data
  &__label,
  &__data {
    font-size: $vl-base-font-size;
    overflow-x: hidden;
    text-overflow: ellipsis;
    padding-bottom: 2rem;

    &--small {
      padding-bottom: .5rem;
    }

    @include respond-to(small) {
      padding-bottom: .5rem;

      &--small {
        padding-bottom: .5rem;
      }
    }
  }

  // Properties label
  &__label {
    flex: 0 0 $vl-property-label-width-column;
    color: $vl-annotation-color;
    padding-right: 1rem;
    max-width: 17rem;

    &--has-input {
      padding-top: 0.7rem;
    }

    @include respond-to(small) {
      flex: 0 0 100%;
      padding-right: 0;
    }
  }

  // Properties data
  &__data {
    flex: 0 0 $vl-property-data-width-column;
    flex-grow: 3;

    @include respond-to(small) {
      flex: 0 0 100%;
      padding-bottom: 1.5rem;
    }
  }

  // Properties column
  &__column {
    display: inline-block;
    width: calc(50% - 1rem);

    @include respond-to(small) {
      width: 100%;
      padding-right: 0;
    }

    + .vl-properties__column {
      padding-right: 0;
      padding-left: 1rem;

      @include respond-to(small) {
        padding-left: 0;
      }
    }

    // Properties column full modifier
    &--full {
      width: 100%;
      padding-left: 0 !important; // sass-lint:disable-line no-important

      .vl-properties__label {
        flex: 0 0 calc(13% - 0.85rem);

        @include respond-to(small) {
          flex: 0 0 40%;
        }
      }

      .vl-properties__data {
        flex: 0 0 85%;

        @include respond-to(small) {
          flex: 0 0 60%;
        }
      }
    }
  }

  .vl-form__buttons {
    padding-top: $vl-spacing-medium;
  }
}
