$vl-fh-top-border-size: 0.3rem;
$vl-fh-padding: 1.5rem;
$vl-fh-padding-small: 0.8rem;
$vl-fh-minheight: 6.5rem;
$vl-fh-top-border-color: darken($vl-alt-bg, 12%);

.vl-functional-header {
  @include u-spacer;
  color: $vl-text-color;
  position: relative;
  min-width: $vl-page-min-width;
  background-color: $vl-alt-bg;
  padding: $vl-fh-padding 0;
  min-height: $vl-fh-minheight;

  @include respond-to(small) {
    min-width: 0;
    margin-bottom: 0;
    padding: $vl-fh-padding-small 0;
    min-height: 0;
  }

  &--has-actions {
    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      height: $vl-fh-top-border-size;
      background-color: $vl-fh-top-border-color;
      z-index: 2;
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;

  }

  .vl-layout {
    position: static;

    @include respond-to(small) {
      padding: 0;
    }
  }

  .vl-title {
    margin-bottom: 0;
    font-size: 2rem;
    line-height: 1;
    margin-bottom: 0.5rem;

    @include respond-to(small) {
      margin-bottom: 0;
    }
  }

  // navigation top right
  &__actions {
    margin-top: -1.2rem;
    padding: 1.5rem 0.5rem 1rem;
    background: $vl-fh-top-border-color;

    @include respond-to(small) {
      margin-top: -0.8rem;
    }
  }

  // single action
  &__action {
    float: left;
    padding: 0 0.5rem;
    font-size: 1.4rem;
    line-height: 1;
    color: $vl-text-color;

    + .vl-functional-header__action {
      border-left: 1px solid $vl-text-color;
    }

    > a,
    > button {
      line-height: inherit;
      color: $vl-text-color;
      margin: 0;
    }
  }

  &__title {
    font-size: 2rem;
    font-family: $vl-accent-font;
    font-weight: 500;
    text-transform: uppercase;
    text-decoration: none;

    &[href] {
      color: inherit;

      &:hover,
      &:focus {
        color: $vl-science-blue;
      }
    }

    @include respond-to(small) {
      font-size: 1.8rem;
    }
  }

  &__content,
  &__menu,
  &__search,
  &__sub__actions {
    @include respond-to(small) {
      padding: 0 1rem;
    }
  }

  &__content {
    display: flex;
    align-items: center;
  }

  // all subcontent in the custom header
  &__sub {
    margin-top: 0.5rem;
    clear: both;
    position: relative;
    border-top: 1px solid $vl-border-color;

    @include respond-to(small) {
      margin-top: $vl-fh-padding-small;
    }

    &__actions {
      display: flex;
      align-items: flex-start;
      font-size: 0; // remove whitespace between inline blocks
      padding-top: 1rem;
    }

    &__action {
      font-size: 1.6rem;
      display: inline-flex;
      vertical-align: middle;
      position: relative;

      @include respond-to(small) {
        font-size: $vl-base-mobile-font-size;
      }

      + .vl-functional-header__sub__action {
        &::before {
          content: "";
          height: 2.4rem;
          width: 1px;
          margin: 0 1rem;
          background-color: $vl-border-color;
          flex: 0 0 auto;

          @include respond-to(small) {
            height: 2rem;
            margin: 0 0.6rem 0 0.8rem;
          }
        }
      }
    }

    &__title {
      font-family: $vl-base-font;
      font-weight: 700;
      font-size: 1.6rem;
      display: inline-block;

      @include respond-to(small) {
        font-size: 1.5rem;
      }
    }
  }

  // Search in the sub section
  &__search {
    text-align: right;
    margin: 0.5rem 0 0;

    @include respond-to(small) {
      margin: 1rem 0 0.5rem;
      text-align: left;
    }

    &__input {
      display: inline-block;
      vertical-align: middle;
      font-size: 1.4rem;
      font-weight: 400;
      width: 100%;
      padding-right: 3.2rem;
      transition: max-width 0.3s ease-in-out;

      &.vl-input-field {
        max-width: 20rem;

        @include respond-to(small) {
          max-width: 100%;
        }

        &:focus {
          max-width: 24rem;

          @include respond-to(small) {
            max-width: 100%;
          }

          + .vl-functional-header__search__submit {
            opacity: 1;
          }
        }
      }

      @include respond-to(small) {
        transition: max-width 0s ease-in-out;
        max-width: 100%;

        &:focus {
          max-width: 100%;
        }
      }
    }

    &__submit {
      position: absolute;
      top: 0;
      right: 0;
      color: $vl-mine-shaft;
      border: 0;
      background: none;
      font-size: 1.6rem;
      outline: none;
      margin: 0.6rem 0 0;
      padding: 0.8rem 1.1rem;
      cursor: pointer;

      transition: opacity 0.1s, color 0.2s ease-in-out;
      opacity: 0.7;

      &:hover,
      &:focus {
        color: $vl-action-color;
        opacity: 1;
      }

      @include respond-to(small) {
        margin: 0.1rem 0 0;
        right: 1rem;
        top: 1rem;
      }
    }
  }

  // Responsive menu

  &__menu {
    position: relative;
    border-bottom: 1px solid $vl-border-color;

    // Open state - toggled in JS
    &--open & {
      &__list {
        max-height: 100%;
        height: auto;
      }

      &__toggle {
        @include vi-cross;
        font-size: 1.3rem;
        border: 0;
      }
    }

    &__list {
      width: calc(100% - 5rem);
      max-height: 4.2rem;
      height: 4.2rem;
      overflow: hidden;
    }

    &__item {
      &:first-child {
        margin: 0.3rem 0 0;
      }

      &:last-child {
        margin: 0 0 0.3rem;
      }
    }

    &__cta {
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      padding: 0.8rem 0;
      text-decoration: none;
      font-size: 1.5rem;

      &:hover {
        text-decoration: underline;
      }
    }

    &__toggle {
      @include vi;
      @include vi-arrow;
      position: absolute;
      top: 0;
      right: 0;
      padding: 1rem 1.3rem;
      font-size: 1.5rem;
      border-left: 1px solid $vl-border-color;
      color: $vl-text-color;
      cursor: pointer;

      &::before {
        display: inline-block;
        transform: rotate(90deg);
        position: relative;
        top: 1px;
      }
    }
  }
}
