$vl-spotlight-vertical-padding: 2rem;
$vl-spotlight-horizontal-padding: 0rem;
$vl-spotlight-alt-padding: 2rem;

.vl-spotlight {
  display: block;
  text-decoration: none;
  position: relative;
  padding: $vl-spotlight-vertical-padding $vl-spotlight-horizontal-padding;

  // border-top
  &::before {
    display: block;
    content: "";
    height: .2rem;
    left: 0;
    right: 0;
    position: absolute;
    top: 0;
    background-color: $vl-border-color;
    z-index: 3;
  }

  &--alt {
    background-color: $vl-alt-bg;
    padding: $vl-spotlight-alt-padding;

    &.vl-spotlight--no-border {
      padding: $vl-spotlight-alt-padding;

      &.vl-spotlight--image {
        padding-top: 0;
      }
    }

    .vl-region--alt & {
      background-color: $vl-page-bg;
    }
  }

  &--no-border {
    padding-top: 0;
    margin-top: 5px; // needed for focus style

    &::before {
      display: none;
    }
  }
}

a {
  &.vl-spotlight {
    color: $vl-action-color;

    &::before {
      background-color: $vl-action-color;
      mix-blend-mode: normal;
    }

    &:focus,
    &:hover {
      color: $vl-action-hover-color;

      &::before {
        background-color: $vl-action-hover-color;
      }

      .vl-spotlight__image__img {
        transform: scale(1.05);
      }

      .vl-spotlight__title {
        text-decoration: underline;
      }
    }
  }
}

.vl-spotlight__header {
  position: relative;
  overflow: hidden;
  margin-top: -$vl-spotlight-vertical-padding;
  margin-left: 0;
  margin-right: 0;

  .vl-spotlight--no-border & {
    margin-top: 0;
  }

  .vl-spotlight--alt & {
    margin-left: -$vl-spotlight-alt-padding;
    margin-right: -$vl-spotlight-alt-padding;
  }
}

.vl-spotlight__image,
.vl-spotlight__color-block,
.vl-spotlight__transparent-block {
  min-height: 22rem;
  padding-top: 45%;
  position: relative;
  overflow: hidden;

  @include respond-to(medium) {
    min-height: 16rem;
  }
}

.vl-spotlight__color-block,
.vl-spotlight__transparent-block {
  text-align: center;

  .vl-icon {
    font-size: 12rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);

    &::before {
      @include pattern;
    }
  }
}

.vl-spotlight__color-block {
  background-color: $vl-accent-color;

  &--alt {
    background-color: darken($vl-alt-bg, 5%);

    .vl-icon {
      &::before {
        @include pattern(darken($vl-alt-bg, 40%));
      }
    }
  }

  &--dark {
    background-color: $vl-alt-bg-dark;

    .vl-icon {
      &::before {
        @include pattern(lighten($vl-alt-bg-dark, 10%));
      }
    }
  }
}

.vl-spotlight__transparent-block {
  background: transparent;
  border: 1px solid $vl-border-color;

  .vl-icon {
    font-size: 12rem;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);

    &::before {
      @include pattern($vl-border-color);
    }
  }

  &--visual {
    padding: 1rem;
    line-height: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.vl-spotlight__image__img {
  @include object-fit;

  transition: transform .2s;
  backface-visibility: hidden;
  transform: translate3d(0, 0, 0);

  .vl-spotlight__image--focus-center-right &,
  .vl-spotlight__image--focus-top-right &,
  .vl-spotlight__image--focus-bottom-right & {
    right: 0;
    left: auto;
    margin-left: 0;
    margin-right: 0;
  }

  .vl-spotlight__image--focus-center-left &,
  .vl-spotlight__image--focus-top-left &,
  .vl-spotlight__image--focus-bottom-left & {
    right: auto;
    left: 0;
    margin-left: 0;
    margin-right: 0;
  }

  .vl-spotlight__image--focus-top-right &,
  .vl-spotlight__image--focus-top-center &,
  .vl-spotlight__image--focus-top-left & {
    top: 0;
    bottom: auto;
    margin-top: 0;
    margin-bottom: 0;
  }

  .vl-spotlight__image--focus-bottom-right &,
  .vl-spotlight__image--focus-bottom-center &,
  .vl-spotlight__image--focus-bottom-left & {
    bottom: 0;
    top: auto;
    margin-top: 0;
    margin-bottom: 0;
  }
}

// special title to partially cover the img
.vl-spotlight__title-wrapper {
  position: relative;
  padding-top: $vl-spotlight-vertical-padding;
  overflow: hidden;
  text-overflow: ellipsis;

  .vl-spotlight--alt &,
  .vl-region--alt & {
    .vl-spotlight__title,
    &::before {
      background-color: $vl-alt-bg;
    }
  }

  .vl-region--alt .vl-spotlight--alt & { // sass-lint:disable-line force-element-nesting
    .vl-spotlight__title,
    &::before {
      background-color: $vl-white;
    }
  }

  .vl-spotlight--alt & {
    padding-left: $vl-spotlight-alt-padding;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: $vl-spotlight-horizontal-padding;
    background-color: $vl-white;

    .vl-spotlight--alt & {
      width: $vl-spotlight-alt-padding;
    }

  }

  .vl-spotlight__title {
    display: inline-flex;
    max-width: calc(100% - 1rem);
    white-space: pre-wrap;
    padding: 0 0.8rem 0.5rem 0;
    background-color: $vl-white;
    word-break: normal;
    hyphens: auto;

    @supports (overflow-wrap: break-word) {
      overflow-wrap: break-word;
    }

    &::after {
      content: "";
      position: absolute;
      top: -0.25em;
      right: 100%;
      bottom: -0.25em;
      width: 0.25em;
    }

    .vl-spotlight--alt & {
      padding-right: $vl-spotlight-alt-padding;
    }
  }
}

.vl-spotlight__title {
  line-height: 1.25;
  position: relative;
  font-size: 2rem;
  font-weight: 500;
  transition: color 0.2s;

  &.vl-link {
    font-weight: 500;
  }

  .vl-spotlight--s & {
    font-size: $vl-base-font-size;

    @include respond-to(small) {
      font-size: $vl-base-font-size--small;
    }
  }

  .vl-spotlight--l & {
    font-size: 2.4rem;

    @include respond-to(small) {
      font-size: 1.8rem;
    }
  }
}

.vl-spotlight__subtitle {
  font-size: 1.5rem;
  text-transform: uppercase;
  font-weight: 500;
  color: $vl-annotation-color;
  padding-top: 0.6rem;

  @include respond-to(small) {
    font-size: 1.3rem;
  }
}

.vl-spotlight__content {
  .vl-document__type {
    padding: 3.6rem 0 0 2rem;
  }
}

// Deprecated
.vl-spotlight__data {
  margin: .7rem 0;
  color: $vl-annotation-color;
}

.vl-spotlight__text {
  margin-top: 1rem;
  font-size: $vl-base-font-size;
  line-height: $vl-base-line-height;
  color: $vl-text-color;
  text-decoration: none;
}
