.vl-u-offset--spacing {
  padding: 0 #{$vl-half-column-offset + "%"} 1rem;
}

.vl-u-offset--left {
  @include offset("left");

  @include respond-to(small) {
    @include offset("none");
  }

  &.vl-u-offset--spacing {
    padding-left: 0;
  }
}

.vl-u-offset--right {
  @include offset("right");

  @include respond-to(small) {
    @include offset("none");
  }

  &.vl-u-offset--spacing {
    padding-right: 0;
  }
}

