.vl-infoblock {

  &--border {
    border: 1px solid $vl-border-color;
    padding: 3rem;

    @include respond-to(medium-up) {
      padding: 5rem;
    }
  }

  &__header {
    @include vi;
    position: relative;
    display: flex;
    align-items: center;
    line-height: 4.5rem;
    overflow-wrap: break-word;

    @include respond-to(small) {
      line-height: 3.6rem;
    }

    &__icon {
      &.vl-icon,
      & {
        display: block;
        flex: 0 0 auto;
        background-color: transparent;
        background: $vl-accent-color;
        font-size: 2.2rem;
        border-radius: 50%;
        color: $vl-text-color;
        width: 4.5rem;
        height: 4.5rem;
        text-align: center;
        margin-right: 2.7rem;
        align-self: start;

        @include respond-to(small) {
          font-size: 1.5rem;
          width: 3.6rem;
          height: 3.6rem;
          margin-right: 1.5rem;
        }
      }

      // Info block contact modifier
      .vl-infoblock--contact & {
        @include vi;
        @include vi-addressbook;
      }

      // Info block publications modifier
      .vl-infoblock--publications & {
        @include vi;
        @include vi-book;
      }

      // Info block faq modifier
      .vl-infoblock--faq & {
        @include vi;
        @include vi-faq;
      }

      // Info block news modifier
      .vl-infoblock--news & {
        @include vi;
        @include vi-news;
      }

      // Info block timeline modifier
      .vl-infoblock--timeline & {
        @include vi;
        @include vi-timeline;
      }

      // Info block question modifier
      .vl-infoblock--question & {
        @include vi;
        @include vi-question;
      }

      @include respond-to(small) {
        font-size: 1.5rem;
        width: 3.6rem;
        height: 3.6rem;
        margin-right: 1.5rem;
      }
    }
  }

  // Info block title
  &__title {
    @include title(4);

    @include respond-to(small) {
      @include title(5);
    }
  }

  // Info block content
  &__content {
    padding-top: 1.5rem;
    margin-left: 7.2rem;

    @include respond-to(small) {
      padding-top: 0;
      margin-left: 5.2rem;
    }
  }
}
