//
// sass-lint:disable no-duplicate-properties
//
@mixin font-face($vl-family, $vl-location, $vl-name, $vl-weight: normal, $vl-style: normal) {
  @font-face {
    font-family: $vl-family;
    src:
    url($vl-location + $vl-name + ".woff2") format("woff2"),
    url($vl-location + $vl-name + ".woff") format("woff");
    font-weight: $vl-weight;
    font-style: $vl-style;
  }
}

@mixin vl-u-text--ellipse {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@mixin vl-u-text--uppercase {
  text-transform: uppercase;
}

@mixin vl-u-text--lowercase {
  text-transform: lowercase;
}

@mixin vl-u-text--capitalize {
  &::first-letter {
    text-transform: uppercase;
  }
}

@mixin vl-u-text--success {
  color: $vl-success-text-color;
}

@mixin vl-u-text--warning {
  color: $vl-warning-text-color;
}

@mixin vl-u-text--error {
  color: $vl-error-text-color;
}

@mixin vl-u-text--bold {
  font-weight: 500;
}

@mixin vl-u-text--italic {
  font-style: italic;
}

@mixin vl-u-text--small {
  font-size: 1.4rem !important; // sass-lint:disable-line no-important
}

@mixin vl-u-text--xsmall {
  font-size: 1.2rem !important; // sass-lint:disable-line no-important
}

@mixin vl-u-text--xxsmall {
  font-size: 1rem !important; // sass-lint:disable-line no-important
}

@mixin vl-u-link-underline {
  color: $vl-action-text-color;
  text-decoration: underline;

  &:hover {
    color: $vl-action-text-hover-color;
    text-decoration: none;
  }

  &:focus,
  &:active {
    color: $vl-action-text-active-color;
    text-decoration: none;
  }
}
