// sass-lint:disable no-color-literals

$vl-mark-box-shadow: inset 0 -10px 0 0;

@mixin vl-u-mark($vl-variant: "default") {
  background-color: transparent;

  @if $vl-variant == "default" {
    box-shadow: #{$vl-mark-box-shadow} rgba($vl-outline-color, 0.3);
  }
  @if $vl-variant == "accent" {
    box-shadow: #{$vl-mark-box-shadow} $vl-accent-color-rgba-30;
  }
  @if $vl-variant == "info" {
    box-shadow: #{$vl-mark-box-shadow} rgba($vl-border-color, 0.4);
  }
  @if $vl-variant == "success" {
    box-shadow: #{$vl-mark-box-shadow} rgba($vl-success-color, 0.2);
  }
  @if $vl-variant == "warning" {
    box-shadow: #{$vl-mark-box-shadow} rgba($vl-warning-color, 0.2);
  }
  @if $vl-variant == "error" {
    box-shadow: #{$vl-mark-box-shadow} rgba($vl-error-color, 0.2);
  }
}
