#vlaanderen-top {
  position: fixed;
  z-index: 10004;
  left: 0;
  right: 0;
  top: 0;
  width: 100%;
  display: block;

  min-width: 768px;
  background: #fff;
  color: #333;
  border-bottom: 1px solid #cbd2da;
  font-size: 16px;
  font-family: "Flanders Art Sans", sans-serif;
  -webkit-text-size-adjust: none;
  font-smoothing: antialiased !important;
  text-rendering: optimizeLegibility;
}

#vlaanderen-navigation {
  white-space: nowrap;
}

#vlaanderen-link {
  display: inline-block;
  font-family: "Flanders Art Sans", sans-serif;
  font-weight: 500;
  text-decoration: none;
  outline: none;

  color: #333332;
  font-size: 1.1875em;
  line-height: 2.31579em;
  text-transform: none;
  transition: color 0.2s;
}

#vlaanderen-link:hover {
  color: #06c;
}

#home-navigation {
  display: inline-block;
  vertical-align: bottom;
  position: relative;
  white-space: nowrap;
}

#home-link {
  display: inline-block;
  position: relative;
  font-family: "Flanders Art Sans", sans-serif;
  font-weight: 500;
  text-decoration: none;
  outline: none;

  color: #333332;
  text-transform: uppercase;
  transition: color 0.2s;
  padding-left: 1.5em;
  margin-left: 1.0em;
  font-size: .875em;
  line-height: 3.14286em;
}

#home-link:hover {
  color: #06c;
}

#home-link:before {
  float: left;
  background-color: #cbd2da !important;
  transform: rotate(-20deg);
  transform-origin: top left;
  display: block;
  content: ' ';
  position: absolute;
  left: 0;
  top: 10px;
  bottom: 10px;
  width: 1px;
}

#vlaanderen-top-logo {
  position: relative;
  width: 47px;
  background-color: #fff;
  overflow: hidden;
  transition: background-color 0.2s;
  background-image: url('data:image/png;base64,');

  float: left;
  margin-right: 5px;
  height: 44px;
  background-repeat: no-repeat;
  background-position: 11px center;
}

#vlaanderen-top-logo:after {
  content: '';
  display: block;
  position: absolute;
  left: 28px;
  top: -4px;
  height: 54px;
  width: 1px;
  background-image: none !important;
  background-color: #cbd2da;
  transform: rotate(-20deg);
  transform-origin: top left;

  /* eslint-disable-next-line */
  background-image: url('data:image/png;base64,');
}

@media (max-width: 580px) {
  #home-link .home-link-vl { display: none !important; }
}