// sass-lint:disable force-element-nesting placeholder-in-extend

.vl-typography ul,
.ul {
  list-style-type: disc;
}

.vl-typography ol,
.ol {
  list-style-type: decimal;
}

.vl-typography ol,
.ol,
.vl-typography ul,
.ul {
  display: block;
  margin: 1.8rem 0 2rem 1rem;
  padding-left: 2rem;

  li {
    display: list-item;
    margin-bottom: 0.5em;
  }

  ul {
    list-style-type: circle;
  }

  ol {
    list-style-type: lower-latin;
  }

  ul,
  ol {
    margin: 0.5em 0 0.5rem 1.5em;
  }
}

.vl-typography p,
.p {
  margin-bottom: 1.8rem;

  &:last-child {
    margin-bottom: 0;
  }
}

// styling for all WYSIWYG tags out there

.vl-typography {
  strong,
  b {
    font-weight: 500;
  }

  em,
  i {
    font-style: italic;
  }

  strike,
  s {
    text-decoration: line-through;
  }

  hr {
    display: block;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    margin-left: auto;
    margin-right: auto;
    border: 0;
    border-top: 1px solid $vl-border-color;
  }

  table {
    @include data-table;
    margin-bottom: 2rem;
  }

  blockquote {
    position: relative;
    padding-left: 9rem;
    font-size: 3.4rem;
    font-family: $vl-base-font;
    font-weight: 400;
    margin-bottom: 2rem;
    line-height: 1.5;

    @include respond-to(small) {
      padding-left: 5rem;
    }

    &::before {
      position: absolute;
      content: "\201C";
      font-size: 9rem;
      left: 0;
      top: 0;
      padding-top: 1rem;
      padding-left: 1rem;
      width: 7rem;
      height: 6rem;
      line-height: 1;
      color: $vl-text-color;
      background: linear-gradient(-110deg, transparent 20px, $vl-accent-color 20px);

      @include respond-to(small) {
        width: 4.6rem;
        font-size: 6rem;
        height: 3.6rem;
        padding-right: 1.5rem;
        padding-top: 0.5rem;
        padding-left: 0.5rem;
        margin-right: 0rem;
      }
    }
  }

  pre {
    padding: 1.5rem;
    background-color: $vl-mine-shaft;
    color: $vl-white;
    display: block;
    white-space: pre;
    overflow-x: scroll;
    tab-size: 2;
    word-break: normal;
    hyphens: none;

    code,
    kdb,
    samp {
      background-color: transparent;
      padding: 0;
    }
  }

  code,
  kdb,
  samp {
    font-family: monospace;
    background: $vl-athens-gray;
    padding: .2rem;
  }

  a {
    @include vl-u-link-underline;
  }
}
