$vl-half-column-offset: 100 / 12 / 2; // Get width of one column, split it in two

// Classes to visually pull-image left or right from it's original-content
@mixin offset($vl-side: "left") {
  @if $vl-side == "left" {
    margin-left: -#{$vl-half-column-offset + "%"};
  } @else if $vl-side == "right" {
    margin-right: -#{$vl-half-column-offset + "%"};
  } @else if $vl-side == "none" {
    margin-left: 0;
    margin-right: 0;
  }
}
